import Layout from '../components/Layout.js'
import HeaderLink from '../components/HeaderLink.js';

export default function Blog(props) {
    return (
        <Layout>
            <h1>My Blog</h1>
            <ul>
                <HeaderLink as={`/p/hello-nextjs`} href={`/post?title="Hello Next.js`} title="Hello Next.js"/>
            </ul>
            <p>Funny story, so I tried to export this project and use it as a GitHub Page.
                But because <a href="https://github.com/zeit/next.js/issues/4998">nextjs doesn't support relative paths or base paths</a> at the time of making this, the "About" header link about will be broken in GitHub Pages.</p>
        </Layout>
    )
}